  const eventHandler = async (event: Event) => {
    switch (event.name) {
      case "applynow":
        try {
          if (
            userAuthStatus?.data?.isAuthorized &&
            userAuthStatus?.data?.isAuthorized
          ) {
            dispatch(
              setPopup({
                show: true,
                data: {
                  container: {
                    name: "applynow",
                    data: singleCourseData,
                    dimensions: {
                      width: "450px",
                      // show: "none",
                    },
                  },
                  type: "custom",
                },
              })
            );
          } else {
            dispatch(
              setPopup({
                show: true,
                data: {
                  container: {
                    name: "login",
                    dimensions: {
                      width: "800px",
                      height: "70%",
                      show: "none"

                    },
                  },
                  type: "custom",
                },
              })
            );
          }
        } catch (error) { }
        break;
      case "shortlist":
        try {
          if (
            userAuthStatus?.data?.isAuthorized &&
            userAuthStatus?.data?.isAuthorized
          ) {
            // setLoadingMap((prevLoadingMap) => ({
            //   ...prevLoadingMap,
            //   [event.data._id]: true,
            // }));
            const values = {
              action: "push",
              courseId: event.data?._id,
            };

            const response = await addToShortList(values);
            if (response.data.success) {
              // dispatch(addShortlisted(response.data.data[0]));
              dispatch(setShortlisted(response.data.data));

              // setLoadingMap((prevLoadingMap) => ({
              //   ...prevLoadingMap,
              //   [event.data._id]: false,
              // }));
            } else {
              // setLoadingMap((prevLoadingMap) => ({
              //   ...prevLoadingMap,
              //   [event.data._id]: false,
              // }));
            }
          } else {
            dispatch(
              setPopup({
                show: true,
                data: {
                  container: {
                    name: "login",
                    dimensions: {
                      width: "800px",
                      height: "70%",
                      show: "none"

                    },
                  },
                  type: "custom",
                },
              })
            );
          }
        } catch (error) {
          console.error("An error occurred while shortlisting:", error);
          // setLoadingMap((prevLoadingMap) => ({
          //   ...prevLoadingMap,
          //   [event.data._id]: false,
          // }));
        }
        break;
      case "delete":
        try {
          const values = {
            action: "pull",
            courseId: event.data?._id,
          };
          const response = await addToShortList(values);
          if (response.data.success) {
            dispatch(setShortlisted(response.data.data));
          }
        } catch (error) {
          // setLoadingMap((prevLoadingMap) => ({
          //   ...prevLoadingMap,
          //   [event.data._id]: false,
          // }));
          console.error("An error occurred while unshortlisting:", error);
        }
        break;
      default:
        break;
    }
  };

